/* CellImage */
.cell-image {
  position: relative;
  height: 180px;
  overflow: hidden;
  margin: -20px;
  margin-bottom: 0px;
  background-color: #eee;
}

.cell-image img {
  position: absolute;
  height: auto;
  width: 100%;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
}

/* CellHeader */
.cell-header {
  color: #333;
  font-size: 16px;
  margin-top: 10px;
  margin-bottom: 10px;
  font-weight: bold;

  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
}

.cell-info {
  font-size: 12px;
  margin-top: auto;
  margin-bottom: auto;
}

.cell-info i {
  margin-left: 10px;
}

/* CellBody */
.cell-body {
  margin-top: 0px;
  overflow: hidden;
  text-overflow: ellipsis;
  //height: 130px;
  border-top: 1px solid #ccc;
  padding-top: 10px;

  font-family: Helvetica;
  font-weight: normal;
  font-size: 13px;
  line-height: 19px;
  color: grey;
}

/* CellFooter */
.cell-footer {
  padding: 20px 0px;
  position: absolute;
  bottom: 0;
}

.cell-footer h6 {
  color: grey;
  margin-bottom: 10px;
  margin-top: 15px;
}

.cell-links {
  margin-top: -10px;
}

.cell-link {
  color: #4F90CD;
  font-weight: bold;
  font-size: 12px;
}

.cell-badges {
  display: flex;
  flex-wrap: wrap;
  margin-top: 0px;
}

.cell-badge {
  padding: 5px 7px;
  margin: 0px 5px 5px 0px;
  font-weight: bold;
  border-radius: 5px;
  border: 2px solid #4F90CD;
  color: #4F90CD;
  font-size: 10px;
}

main {
  background-color: white;
  font-family: Helvetica, sans-serif;
  font-weight: 300;
  font-size: 16px;
  line-height: 1.7;
}

.grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: stretch;
  margin: 0px auto;
}

body {
  background-color: #eee;
}

a {
  color: #4F90CD;
  text-decoration: none;
  font-weight: normal;
}

p {
  margin-top: 20px;
  margin-bottom: 20px;
}

.container-small {
  max-width: 800px;
  margin: 0px auto;
  padding: 40px 20px;
}

.container-medium {
  max-width: 1100px;
  margin: 0px auto;
  padding: 40px 20px;
}


/* Currently disabled */

.filter {
  float: left;
  width: 20%;
  background-color: green;
  margin-right: 15px;
  margin-top: 15px;
}

@media only screen and (max-width: 768px) {
    .filter {
      display: none;
    }
}

